'use client';

import { useState } from 'react';
import axios from 'axios';
import Image from 'next/image';
import ReactQuill from './components/ReactQuillClient';
import 'react-quill/dist/quill.snow.css';

// âœ… Types
type Video = {
  id: {
    videoId: string;
  };
  snippet: {
    title: string;
    publishedAt: string;
    thumbnails: {
      medium: {
        url: string;
      };
    };
  };
};

type Blog = {
  title: string;
  content: string;
  highlights: string[];
  tips: string[];
};

export default function YouTubeGallery() {
  const [channelInput, setChannelInput] = useState('');
  const [videos, setVideos] = useState<Video[]>([]);
  const [selectedVideos, setSelectedVideos] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);
  const [editableBlog, setEditableBlog] = useState<Blog | null>(null);

  const getChannelIdFromQuery = async (query: string): Promise<string | null> => {
    const YOUTUBE_API_KEY = process.env.NEXT_PUBLIC_YOUTUBE_API_KEY;
    const url = `https://www.googleapis.com/youtube/v3/search?key=${YOUTUBE_API_KEY}&q=${encodeURIComponent(
      query
    )}&type=channel&part=snippet`;
    try {
      const res = await axios.get(url);
      return res.data.items[0]?.snippet?.channelId || null;
    } catch (err) {
      console.error('Error fetching channel ID:', err);
      return null;
    }
  };

  const getRecentVideos = async (channelId: string): Promise<void> => {
    setLoading(true);
    const YOUTUBE_API_KEY = process.env.NEXT_PUBLIC_YOUTUBE_API_KEY;
    const url = `https://www.googleapis.com/youtube/v3/search?key=${YOUTUBE_API_KEY}&channelId=${channelId}&part=snippet&order=date&maxResults=6`;
    try {
      const res = await axios.get(url);
      setVideos(res.data.items);
    } catch (err) {
      console.error('Error fetching videos:', err);
    }
    setLoading(false);
  };

  const fetchVideos = async () => {
    const channelId = await getChannelIdFromQuery(channelInput);
    if (!channelId) return;
    await getRecentVideos(channelId);
  };

  const toggleVideoSelection = (videoId: string) => {
    setSelectedVideos((prev) =>
      prev.includes(videoId) ? prev.filter((id) => id !== videoId) : [...prev, videoId]
    );
  };

  const generateCombinedBlog = async () => {
    const urls = selectedVideos.map((id) => `https://www.youtube.com/watch?v=${id}`);
    try {
      const res = await axios.post<Blog>('http://127.0.0.1:8000/generate-blog', {
        youtube_urls: urls,
      });
      setEditableBlog(res.data);
    } catch (err) {
      console.error('Blog generation failed', err);
    }
  };

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">YouTube Blog Generator</h1>

      <div className="flex gap-2 mb-4">
        <input
          type="text"
          placeholder="Enter YouTube Channel Name or URL"
          value={channelInput}
          onChange={(e) => setChannelInput(e.target.value)}
          className="border px-3 py-2 rounded w-full"
        />
        <button onClick={fetchVideos} className="bg-blue-600 text-white px-4 py-2 rounded">
          Fetch Videos
        </button>
      </div>

      {loading && <p>Loading videos...</p>}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {videos.map((video) => (
          <div key={video.id.videoId} className="border rounded p-3">
            <Image
              src={video.snippet.thumbnails.medium.url}
              alt={video.snippet.title}
              width={320}
              height={180}
              className="rounded mb-2"
            />
            <div className="flex items-center gap-2 mb-2">
              <input
                type="checkbox"
                checked={selectedVideos.includes(video.id.videoId)}
                onChange={() => toggleVideoSelection(video.id.videoId)}
              />
              <h2 className="font-semibold text-lg">{video.snippet.title}</h2>
            </div>
            <p className="text-sm text-gray-600">{video.snippet.publishedAt}</p>
          </div>
        ))}
      </div>

      {selectedVideos.length > 0 && (
        <button
          className="mt-4 bg-purple-600 text-white px-4 py-2 rounded"
          onClick={generateCombinedBlog}
        >
          Generate Combined Blog ({selectedVideos.length})
        </button>
      )}

      {editableBlog && (
        <div className="mt-8 p-4 border rounded bg-gray-100 space-y-4">
          <input
            className="text-xl font-bold w-full border-b"
            value={editableBlog.title}
            onChange={(e) => setEditableBlog({ ...editableBlog, title: e.target.value })}
          />
          <ReactQuill
            value={editableBlog.content}
            onChange={(val) => setEditableBlog({ ...editableBlog, content: val })}
          />

          <div>
            <h3 className="font-semibold">Highlights:</h3>
            {editableBlog.highlights.map((h, i) => (
              <input
                key={i}
                className="w-full border px-2 py-1 mt-1"
                value={h}
                onChange={(e) => {
                  const newHighlights = [...editableBlog.highlights];
                  newHighlights[i] = e.target.value;
                  setEditableBlog({ ...editableBlog, highlights: newHighlights });
                }}
              />
            ))}
          </div>

          <div className="mt-4">
            <h3 className="font-semibold">Tips:</h3>
            {editableBlog.tips.map((t, i) => (
              <input
                key={i}
                className="w-full border px-2 py-1 mt-1"
                value={t}
                onChange={(e) => {
                  const newTips = [...editableBlog.tips];
                  newTips[i] = e.target.value;
                  setEditableBlog({ ...editableBlog, tips: newTips });
                }}
              />
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
